.barWrap {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 12px;
  display: flex;
  align-items: center;
}
.appsWrap {
  max-width: 1274px;
  margin: 0 auto;
  padding: 0 76px;
  height: 100%;
  .appsPanel {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    .appsSwiper {
      width: 100%;
      position: relative;
      transition-duration: .3s;
      transition-property: transform;
      transition-timing-function: cubic-bezier(.4,0,.2,1);
      .appSwiper {
        width: 100%;
        position: relative;
        .appPanel {
          width: 100%;
          position: relative;
          height: 100%;
          overflow-y: auto;
          &::-webkit-scrollbar {
            display: none;
          }
          .appGrid {
            position: absolute;
            width: 100%;
            --app-size: 64px;
            --app-gap-x: 32px;
            --app-gap-y: 32px;
            display: grid;
            grid-template-columns: repeat(auto-fill, var(--app-size));
            grid-template-rows: repeat(auto-fill, var(--app-size));
            grid-auto-flow: dense;
            grid-gap: var(--app-gap-y) var(--app-gap-x);
            justify-content: center;
            padding: 16px 0;
            .appItem {
              pointer-events: auto;
              grid-column: span 1;
              grid-row: span 1;
              width: var(--app-size);
              height: var(--app-size);
              transition: all 0.3s;
              transform: translateY(0);
              &:hover {
                transform: translateY(-8px);
                .appIcon {
                  box-shadow: 0 0 10px rgba(0,0,0,0.30);
                }
                .appText {
                  transform: translateY(4px);
                  opacity: 1;
                }
              }
              &.appItem2x2 {
                grid-column: span 2;
                grid-row: span 2;
                width: calc(var(--app-size) * 2 + var(--app-gap-y));
                height: calc(var(--app-size) * 2 + var(--app-gap-x));
              }
              &.appItem2x4 {
                grid-column: span 4;
                grid-row: span 2;
                width: calc(var(--app-size) * 4 + var(--app-gap-y) * 3);
                height: calc(var(--app-size) * 2 + var(--app-gap-x));
              }
              .appIcon {
                width: 100%;
                height: 100%;
                border-radius: 20px;
                line-height: var(--app-size);
                text-align: center;
                font-size: 16px;
                overflow: hidden;
                cursor: pointer;
                transition: all 0.3s;
                & > img {
                  width: 100%;
                  height: 100%;
                  object-fit: contain;
                }
              }
              .appText {
                font-size: 12px;
                text-align: center;
                color: #fff;
                text-shadow: 0 0 4px rgba(0,0,0,0.65);
                transform: translateY(-100%);
                opacity: 0;
                transition: all 0.3s;
                cursor: pointer;
              }
            }
          }
        }
      }
    }
  }
}
